import React from 'react';

const About = () => {
    return(
        <div>
            <h1 className="ui header">About</h1>
            <div className="ui middle aligned two column centered grid">
                <div className="column">
                    <div className="row">
                        <p>
                            I'm a professional  with skills in computational optimization and programming. Currently, I'm a Lecturer at <em>Escuela Superior Politécnica del Litoral</em> (ESPOL) in Guayaquil, Ecuador where I teach courses in the area of Mathematical Optimization at Facultad de Ciencias Naturales y Matemáticas.
                        </p>
                    </div>
                    <div className="row">
                        <div className="ui divider"></div>
                    </div>
                    <div className="row">
                        <div className="row">
                            <h3 className="ui header">Interests</h3>
                        </div>
                        <div className="ui hidden divider"></div>
                        <div className="row">
                            <div className="ui bulleted list">
                                <div className="item">
                                    Optimization under Uncertainty
                                </div>
                                <div className="item">
                                    Deep Reinforcement Learning
                                </div>
                                <div className="item">
                                    Sequential Decision Making
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="ui divider"></div>
                    </div>
                    <div className="row">
                        <h3 className="ui header">Publications</h3>
                        <div className="ui hidden divider"></div>
                        <div className="row">
                            <div className="ui bulleted list">
                                <div className="item">
                                    <div className="header">
                                        Application of the Cross-Entropy Method to the Dynamic Assortment Optimization Problem
                                    </div>
                                    Jose Manuel Vera
                                    <div className="description">
                                        <em>Matemática</em>, vol. 18, no. 1, 2020. [<a href="http://www.revistas.espol.edu.ec/index.php/matematica/article/view/696">
                                        link
                                    </a>]
                                    </div> 
                                </div>
                                <div className="item">
                                    <div className="header">
                                        Deep Reinforcement Learning for Routing a Heterogeneous Fleet of Vehicles
                                    </div>
                                    Jose Manuel Vera, Andres G. Abad
                                    <div className="description">
                                        <em>IEEE Latin American Conference on Computational Intelligence (LA-CCI)</em>, 2019.  [<a href="https://ieeexplore.ieee.org/abstract/document/9037042">
                                    link
                                    </a>]
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
                <div className="column">
                    
                    <img alt="railway" className="ui medium rounded image" src={require("../images/photo_2.jpg")}/>
                </div>
            </div>
        </div>
    )
}

export default About;