import React from 'react';
import {BrowserRouter, Route} from 'react-router-dom';
import Header from './Header';
import Home from './Home';
import NotesMenu from './NotesMenu';
import Note from './Note';
import BiseccionApp from './BiseccionApp';
import NewtonApp from './NewtonApp';
import VrpApp from './VrpApp';
import About from './About';

const App = () => {

    return (
        <div >
            <BrowserRouter>
                <div className="ui container">
                    <Header/>
                    <Route path="/" exact component={Home}/>
                    <Route path="/about" exact component={About}/>
                    <Route path="/rn" exact component={NotesMenu}/>
                    <Route path="/interiorpoint" exact><Note name="interior_point"/></Route>
                    <Route path="/biseccion" exact component={BiseccionApp}/>
                    <Route path="/newton" exact component={NewtonApp}/>
                    <Route path="/tsp" exact component={VrpApp}/>
                    
                </div>
            </BrowserRouter>
        </div>

    );
}

export default App;